@keyframes trait {
  to {
    width: 100%;
  }
}

.sort-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sort-range {
  display: grid;
  place-items: center;
  width: 100%;
  input[type="range"] {
    width: 80%;
  }
}

.countries_title {
  margin: auto;
  margin-block: 50px;
  font-size: 3rem;
  font-weight: bold;
  width: fit-content;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: -5px;
    height: 6px;
    width: 0%;
    left: 50%;
    transform: translateX(-50%);
    background-color: orange;
    animation: trait 600ms forwards
      cubic-bezier(
        0.17,
        0.67,
        0.41,
        1.55
      );
  }
}

.radios__container {
  margin-top: 40px;
  display: flex;
  justify-content: space-evenly;
  width: 80%;
}

.countries {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.cancel {
  h3 {
    margin-top: 50px;
    background-color: orange;
    box-shadow: black 0px 0px 0px 3px,
      white 0px 0px 0px 3px,
      black 0px 0px 0px 6px;
    padding: 5px;
    transform: scale(0.9);
    transition: 400ms;
    cursor: pointer;
    font-size: 1.3rem;
  }

  &:hover {
    transform: scale(1);
  }
}
