.card {
  flex: 1 1 300px;
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  position: relative;

  &:hover .flag {
    filter: blur(5px) grayscale(80%);
  }

  &:hover .data-country {
    opacity: 1;
    pointer-events: none;
  }

  &:hover .data-country li {
    opacity: 1;
    transform: translateX(0);
  }

  .flag {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin-top: auto;
    transition: 400ms;
    box-shadow: black 0 0 5px;
  }

  .data-country {
    position: absolute;
    inset: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 1;
    transition: opacity 400ms;
    z-index: 10;
    color: orange;
    overflow: hidden;

    li {
      display: flex;
      background-color: black;
      text-align: center;
      width: 70%;
      transform: translateX(200%);
      transition: 400ms
        cubic-bezier(
          0.17,
          0.67,
          0.16,
          1.32
        );

      &:nth-child(even) {
        color: black;
        background-color: orange;
      }

      &:nth-child(2) {
        transition-delay: 100ms;
      }

      &:nth-child(3) {
        transition-delay: 200ms;
      }

      &:nth-child(4) {
        transition-delay: 300ms;
      }
    }

    span {
      margin-left: auto;
    }
  }
}
