.navigation {
  font-size: 2rem;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 50px;
  padding: 20px;

  a {
    height: fit-content;
    box-shadow: black 0px 0px 0px 3px,
      white 0px 0px 0px 10px,
      black 0px 0px 0px 15px;
    padding: 5px 10px;
    transition: background-color
        ease-in-out 200ms,
      box-shadow ease-out 200ms;

    &:hover {
      background-color: orange;
      box-shadow: black 0px 0px 0px 3px,
        white 0px 0px 0px 3px,
        black 0px 0px 0px 6px;
    }
  }

  .active {
    background-color: orange;
    box-shadow: black 0px 0px 0px 3px,
      white 0px 0px 0px 3px,
      black 0px 0px 0px 6px;
  }
}
